import debounce from 'lodash.debounce';

/**
 * Genel debounce utility fonksiyonlarƒ±
 * Sunucu y√ºk√ºn√º azaltmak i√ßin kullanƒ±lƒ±r
 */

/**
 * Arama i√ßin optimize edilmi≈ü debounce
 * 300ms - Kullanƒ±cƒ±ya akƒ±cƒ± gelir, sunucu y√ºk√ºn√º %90 azaltƒ±r
 */
export const debounceSearch = <T extends (...args: any[]) => any>(
    func: T,
    wait: number = 300
): ((...args: Parameters<T>) => void) => {
    return debounce(func, wait);
};

/**
 * Filtreleme i√ßin optimize edilmi≈ü debounce
 * 400ms - Hƒ±zlƒ± tƒ±klamalarƒ± tek sorguda birle≈ütirir
 */
export const debounceFilter = <T extends (...args: any[]) => any>(
    func: T,
    wait: number = 400
): ((...args: Parameters<T>) => void) => {
    return debounce(func, wait);
};

/**
 * Auto-save i√ßin optimize edilmi≈ü debounce
 * 2000ms - Kullanƒ±cƒ± yazmayƒ± bitirince kaydet
 */
export const debounceAutoSave = <T extends (...args: any[]) => any>(
    func: T,
    wait: number = 2000
): ((...args: Parameters<T>) => void) => {
    return debounce(func, wait);
};

/**
 * Scroll event'leri i√ßin optimize edilmi≈ü debounce
 * 150ms - Smooth scroll deneyimi
 */
export const debounceScroll = <T extends (...args: any[]) => any>(
    func: T,
    wait: number = 150
): ((...args: Parameters<T>) => void) => {
    return debounce(func, wait);
};

/**
 * Genel ama√ßlƒ± debounce
 * √ñzel kullanƒ±m senaryolarƒ± i√ßin
 */
export const createDebounce = <T extends (...args: any[]) => any>(
    func: T,
    wait: number
): ((...args: Parameters<T>) => void) => {
    return debounce(func, wait);
};

/**
 * Throttle utility (debounce'dan farklƒ± - ilk √ßaƒürƒ±yƒ± hemen √ßalƒ±≈ütƒ±rƒ±r)
 * Real-time g√ºncellemeler i√ßin ideal
 */
export const throttle = <T extends (...args: any[]) => any>(
    func: T,
    wait: number
): ((...args: Parameters<T>) => void) => {
    return debounce(func, wait, { leading: true, trailing: false });
};
